.hidden {
  display: none;
}

.hide {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: visibility 0s linear 0.2s,opacity 0.2s linear;
  transition: visibility 0s linear 0.2s,opacity 0.2s linear;
}

.visible {
  visibility: visible;
  opacity: 1;
  -webkit-transition: visibility 0s linear 0.2s,opacity 0.2s linear;
  transition: visibility 0s linear 0.2s,opacity 0.2s linear;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.loader {
  color: #bfbfbf;
  font-size: 50px;
  text-indent: -9999em;
  overflow: hidden;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  margin: 72px auto;
  position: relative;
